@charset "UTF-8";
/* stylelint-disable */
@font-face {
  font-family: "Popins-reg";
  src: url("../fonts/../fonts/Poppins-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
@font-face {
  font-family: "Popins-semb";
  src: url("../fonts/../fonts/Poppins-SemiBold.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
.burgundy-btn {
  color: #FFFFFF;
  border-radius: 8px;
  background: #691A2E;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.burgundy-btn:hover {
  background: #521424;
}

.orange-btn {
  border-radius: 8px;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  color: black;
  transition: background-color 0.3s ease;
}
.orange-btn:hover {
  background: #C26226;
}

.grey-btn {
  border-radius: 8px;
  color: #FFFFFF;
  background: rgba(255, 255, 255, 0.2);
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn:hover {
  background: rgba(255, 255, 255, 0.1);
}

.grey-btn-two {
  border-radius: 8px;
  color: black;
  background: #E1712D;
  box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
  transition: background-color 0.3s ease;
}
.grey-btn-two:hover {
  background: #C26226;
}

* {
  padding: 0;
  margin: 0;
  border: 0;
}

*::-webkit-scrollbar {
  width: 0;
  height: 0;
}

*,
*:before,
*:after {
  -moz-box-sizing: border-box;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
}

:focus,
:active {
  outline: none;
}

a:focus,
a:active {
  outline: none;
}

nav,
footer,
header,
aside {
  display: block;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 100%;
  line-height: 1;
  font-size: 14px;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  -webkit-text-size-adjust: 100%;
}

input,
button,
textarea {
  font-family: inherit;
}

input::-ms-clear {
  display: none;
}

button {
  cursor: pointer;
}

button::-moz-focus-inner {
  padding: 0;
  border: 0;
}

a,
a:visited {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

ul li {
  list-style: none;
}

img {
  vertical-align: top;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
}

img,
picture {
  width: auto;
  height: auto;
  max-width: 100%;
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100%;
}

.main {
  flex: 1 1 auto;
}

@media screen and (max-width: 1024px) {
  .wrapper {
    overflow: hidden;
  }
}
.title {
  font-family: "Popins-semb", sans-serif;
  color: #111111;
  font-size: 24px;
  line-height: 36px;
}
@media screen and (max-width: 600px) {
  .title {
    font-size: 20px;
    line-height: 30px;
  }
}

[class*=__container] {
  max-width: 1270px;
  margin: 0 auto;
  padding: 0 15px;
}

body {
  font-family: "Popins-reg", sans-serif;
  font-style: normal;
  font-size: 20px;
  letter-spacing: 0.02em;
  color: #111111;
  background: #FCEDF0;
}
body._lock {
  overflow: hidden;
}

.menu__icon {
  display: none;
}
@media screen and (max-width: 1024px) {
  .menu__icon {
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: inset 0px -4px 0px rgba(0, 0, 0, 0.1), inset 0px 4px 0px rgba(255, 255, 255, 0.2);
    border-radius: 8px;
    width: 44px;
    height: 44px;
    position: relative;
    z-index: 5;
  }
  .menu__icon::before {
    content: "";
    position: absolute;
    width: 20px;
    height: 3px;
    background: #FFFFFF;
    border-radius: 8px;
    top: 11px;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    transition: 0.3s ease;
  }
  .menu__icon._active::before {
    opacity: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) rotate(-45deg);
  }
  .menu__icon::after {
    content: "";
    position: absolute;
    width: 20px;
    height: 3px;
    background: #FFFFFF;
    border-radius: 8px;
    bottom: 11px;
    left: 50%;
    transform: translate(-50%, 0);
    opacity: 0;
    transition: 0.3s ease;
  }
  .menu__icon._active::after {
    opacity: 1;
    bottom: 50%;
    left: 50%;
    transform: translate(-50%, 50%) rotate(45deg);
  }
  .menu__icon._active img {
    opacity: 0;
  }
}

.header {
  position: absolute;
  width: 100%;
  top: 0;
  left: 0;
  padding: 17px 0;
}
.header__row {
  display: flex;
  align-items: center;
}
.header__logo {
  flex: 1 1 auto;
}
.header__menu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
@media screen and (max-width: 1024px) {
  .header__menu {
    padding: 95px 15px 0;
    flex-direction: column-reverse;
    align-items: stretch;
    justify-content: flex-end;
    position: fixed;
    top: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(189.48deg, #20030B -42.96%, #480F1F 92.84%);
    overflow: auto;
    left: 0;
    z-index: 4;
    transition: top 0.3s ease 0s;
  }
  .header__menu._active {
    top: 0;
  }
}
.header__nav {
  flex: 1 1 auto;
}
@media screen and (max-width: 1024px) {
  .header__nav {
    flex: 0 1 auto;
    max-width: 500px;
    margin: 0 auto;
    width: 100%;
  }
}
.header__search {
  position: relative;
  display: flex;
  align-items: center;
}
.header__search-input {
  position: absolute;
  font-family: "Popins-reg", sans-serif;
  display: inline-block;
  width: 350px;
  padding: 10px 10px 10px 30px;
  font-size: 17px;
  line-height: 30px;
  border-radius: 12px;
  background: #FFFFFF;
  border: 1.5px solid #808080;
  color: #808080;
  right: -6px;
  top: 50px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.header__search-input._active {
  width: 350px;
  opacity: 1;
  visibility: visible;
}
.header__search-btn {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 40px;
  height: 40px;
  background: #FFFFFF;
  border-radius: 12px;
  cursor: pointer;
  position: relative;
  z-index: 2;
  transition: background-color 0.3s ease;
}
.header__search-btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(45deg);
  width: 25px;
  height: 3px;
  border-radius: 5px;
  background-color: #E1712C;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.header__search-btn::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 25px;
  border-radius: 5px;
  height: 3px;
  background-color: #E1712C;
  opacity: 0;
  transition: opacity 0.3s ease;
}
.header__search-btn._active::before, .header__search-btn._active::after {
  opacity: 1;
}
.header__search-btn:hover {
  background: #c5c3c3;
}
.header__search-image {
  fill: #E1712C;
  transition: opacity 0.3s ease;
  opacity: 1;
}
.header__search-image._active {
  opacity: 0;
}

@media screen and (max-width: 1024px) {
  .header__search {
    margin: 0 auto 10px;
    max-width: 500px;
    width: 100%;
  }
  .header__search-input {
    border: none;
    display: block;
    opacity: 1;
    visibility: visible;
    position: static;
    width: 100%;
    padding: 16px 13px 14px 45px;
    font-size: 17px;
    line-height: 25px;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 12px;
  }
  .header__search-input::placeholder {
    color: #FFFFFF;
  }
  .header__search-btn {
    background: transparent;
    position: absolute;
    left: 5px;
    top: 50%;
    transform: translateY(-50%);
  }
  .header__search-btn:hover {
    background: transparent;
  }
  .header__nav {
    overflow-y: scroll;
    padding: 60px 0 60px 0;
  }
}
.menu__list {
  display: flex;
  justify-content: center;
  align-items: center;
}
.menu__item {
  position: relative;
}
.menu__item:not(:last-child) {
  margin: 0 35px 0 0;
}
.menu__item-sub_list {
  position: relative;
  z-index: 1;
  display: flex;
  align-items: center;
}
.menu__item-sub_list:hover .menu__link {
  color: #c5c3c3;
}
.menu__item-sub_list:hover::before {
  content: "";
}
.menu__item-sub_list:hover span::after {
  background: url("../img/polygon-hover.svg") no-repeat 50% 0/contain;
}
.menu__item-sub_list::before {
  position: absolute;
  background: transparent;
  cursor: pointer;
  width: 100%;
  height: 50px;
  top: 0;
  left: 0;
  z-index: 0;
}
.menu__item-sub_list span {
  display: flex;
  align-items: center;
}
.menu__item-sub_list span::after {
  content: "";
  display: inline-block;
  background: url("../img/polygon.svg") no-repeat 50% 0/contain;
  width: 11px;
  height: 7px;
  margin-left: 10px;
  margin-top: -2px;
}
.menu__item-item {
  position: relative;
}
.menu__item-item::after {
  content: "";
  display: inline-block;
  background: url("../img/polygon-hover.svg") no-repeat 50% 0/contain;
  width: 11px;
  height: 7px;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%) rotate(-90deg);
  pointer-events: none;
}
.menu__item-list {
  position: absolute;
  z-index: 1;
  min-width: 220px;
  max-width: 220px;
  top: 30px;
  left: 0;
  display: none;
  padding: 10px 20px;
  background: linear-gradient(180deg, #FCEDF0 0%, #FFFFFF 29.32%);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.menu__item-link {
  font-family: "Popins-reg", sans-serif;
  color: #111111;
  font-size: 15px;
  line-height: 34px;
  display: inline-block;
  transition: color 0.3s ease;
  width: 100%;
}
.menu__item-link:hover {
  color: #E1712C;
}
.menu__item-hover-desc:hover .menu__item-list {
  display: block;
}
.menu__item-sub_list-2 span, .menu__item-sub_list-3 span {
  cursor: pointer;
}
.menu__item-sub_list-2 span::after, .menu__item-sub_list-3 span::after {
  content: none !important;
}
.menu__item-list-2 {
  position: absolute;
  z-index: 3;
  min-width: 220px;
  max-width: 220px;
  top: -7px;
  left: 204px;
  display: none;
  padding: 10px 20px;
  background: linear-gradient(180deg, #FCEDF0 0%, #FFFFFF 29.32%);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.menu__item-hover-desc-2 {
  position: relative;
}
.menu__item-hover-desc-2::before {
  position: absolute;
  background: transparent;
  cursor: pointer;
  width: 23%;
  height: 28px;
  top: 3px;
  right: -40px;
  z-index: 0;
}
.menu__item-hover-desc-2:hover::before {
  content: "";
}
.menu__item-hover-desc-2:hover .menu__item-list-2 {
  display: block;
}
.menu__item-list-3 {
  position: absolute;
  z-index: 3;
  min-width: 220px;
  max-width: 220px;
  top: -7px;
  left: 204px;
  display: none;
  padding: 10px 20px;
  background: linear-gradient(180deg, #FCEDF0 0%, #FFFFFF 29.32%);
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.2);
  border-radius: 8px;
}
.menu__item-hover-desc-3 {
  position: relative;
}
.menu__item-hover-desc-3::before {
  position: absolute;
  background: transparent;
  cursor: pointer;
  width: 23%;
  height: 28px;
  top: 3px;
  right: -40px;
  z-index: 0;
}
.menu__item-hover-desc-3:hover::before {
  content: "";
}
.menu__item-hover-desc-3:hover .menu__item-list-3 {
  display: block;
}
.menu__link {
  cursor: pointer;
  position: relative;
  z-index: 2;
  font-family: "Popins-reg", sans-serif;
  font-size: 18px;
  color: #FFFFFF;
  transition: color 0.3s ease;
}
.menu__link:hover {
  color: #c5c3c3;
}
@media screen and (max-width: 1024px) {
  .menu__link {
    font-size: 22px;
    line-height: 24px;
  }
}

@media screen and (max-width: 1024px) {
  .menu__list {
    flex-direction: column;
    align-items: flex-start;
  }
  .menu__item {
    width: 100%;
  }
  .menu__item:not(:last-child) {
    margin: 0 0 25px 0;
  }
  .menu__item-sub_list {
    flex-direction: column;
    align-items: flex-start;
    position: relative;
  }
  .menu__item-sub_list._open span::after {
    transform: rotate(180deg);
  }
  .menu__item-sub_list:hover::before {
    content: none;
  }
  .menu__item-sub_list:hover .menu__link {
    color: #FFFFFF;
  }
  .menu__item-list, .menu__item-list-2, .menu__item-list-3 {
    max-width: 100%;
    width: 100%;
    position: static;
    display: none;
    background: transparent;
    box-shadow: none;
    padding: 20px 0;
    border-top: 1px solid rgba(255, 255, 255, 0.14);
    border-bottom: 1px solid rgba(255, 255, 255, 0.14);
    border-radius: 0;
    margin-top: 20px;
  }
  .menu__item-list._open, .menu__item-list-2._open, .menu__item-list-3._open {
    display: block;
  }
  .menu__item-list {
    padding: 20px 0 20px 5px;
  }
  .menu__item-list-2 {
    padding: 20px 0 20px 15px;
  }
  .menu__item-list-3 {
    padding: 20px 0 20px 30px;
  }
  .menu__item-item::after {
    top: 6px;
    transform: translateY(0) rotate(-90deg);
  }
  .menu__item-item:not(:last-child) {
    margin: 0 0 20px 0;
  }
  .menu__item-sub_list-2::after, .menu__item-sub_list-3::after {
    transform: translateY(0) rotate(0deg);
  }
  .menu__item-link {
    color: #FFFFFF;
    font-size: 17px;
    line-height: 20px;
  }
}
.casino {
